        cancelTarget: null,
        isCancelling: false,
        // Dropdown state management
        openDropdownId: null,
        dropdownCoords: { top: 0, left: 0 },
        dropdownPlacement: 'below', // 'below' | 'above' based on available space
        dropdownWidth: 192,
        // Track whether current assignment intent is a reassignment
        isReassign: false
      }
    },
    computed: {
      // Assign is allowed only if request is not yet implemented and there is no active assignment
      canAssign() {
        const status = this.requestInfo?.ict_officer_status || this.requestInfo?.status
        const isImplemented = status === 'implemented'
        return !isImplemented
      },
      currentAssignee() {
        if (!this.requestInfo?.assigned_ict_officer_id) {
          // Fallback: infer from officers list
          const inferred = this.ictOfficers.find((o) => o.status === 'Assigned')
          return inferred || null
        }
        return (
          this.ictOfficers.find((o) => o.id === this.requestInfo.assigned_ict_officer_id) || null
        )
      },
      hasActiveIctAssignment() {
        return !!(
          this.requestInfo?.assigned_ict_officer_id ||
          this.ictOfficers.some((o) => o.status === 'Assigned')
        )
      },
      filteredOfficers() {
        let filtered = [...this.ictOfficers]

        // Filter by search query
        if (this.searchQuery.trim()) {
          const query = this.searchQuery.toLowerCase()
          filtered = filtered.filter(
            (officer) =>
              (officer.name || '').toLowerCase().includes(query) ||
              (officer.pf_number || '').toLowerCase().includes(query) ||
              (officer.phone_number || '').toLowerCase().includes(query)
          )
        }

        // Filter by status
        if (this.statusFilter) {
          filtered = filtered.filter(
            (officer) => (officer.status || 'Available') === this.statusFilter
          )
        }

        // Sort by availability (Available first, then by name)
        return filtered.sort((a, b) => {
          const statusA = a.status || 'Available'
          const statusB = b.status || 'Available'

          if (statusA === 'Available' && statusB !== 'Available') return -1
          if (statusA !== 'Available' && statusB === 'Available') return 1

          return (a.name || '').localeCompare(b.name || '')
        })
      }
    },
    async mounted() {
      this.requestId = this.$route.params.id
      if (!this.requestId) {
        this.error = 'No request ID provided'
        return
      }

      await Promise.all([this.fetchRequestInfo(), this.fetchIctOfficers()])

      // Add click listener to close dropdowns when clicking outside
      document.addEventListener('click', this.closeAllDropdowns)
    },

    beforeUnmount() {
      // Clean up event listeners
      document.removeEventListener('click', this.closeAllDropdowns)
    },
    methods: {
      async fetchRequestInfo() {
        try {
          const result = await headOfItService.getRequestById(this.requestId)
          if (result.success) {
            this.requestInfo = result.data
          }
        } catch (error) {
          console.error('Error fetching request info:', error)
        }
      },

      async fetchIctOfficers() {
        this.isLoading = true
        this.error = null

        try {
          const result = await headOfItService.getIctOfficers(this.requestId)
          if (result.success) {
            this.ictOfficers = result.data || []
          } else {
            this.error = result.message
          }
        } catch (error) {
          this.error = 'Failed to load ICT officers'
        } finally {
          this.isLoading = false
        }
      },

      async refreshOfficers() {
        await this.fetchIctOfficers()
      },

      confirmAssignTask(officer, options = {}) {
        this.selectedOfficer = officer
        this.isReassign = options.reassign === true
        this.showConfirmModal = true
      },

      async assignTask() {
        if (!this.selectedOfficer) return

        this.isAssigning = true

        try {
          const result = await headOfItService.assignTaskToIctOfficer(
            this.requestId,
            this.selectedOfficer.id,
            this.selectedOfficer?.phone_number || this.selectedOfficer?.phone || null,
            this.isReassign
          )

          if (result.success) {
            this.assignedOfficer = this.selectedOfficer
            this.showConfirmModal = false
            this.showSuccessModal = true
            this.isReassign = false

            // Ensure UI knows there is an active assignment
            this.requestInfo = {
              ...(this.requestInfo || {}),
              assigned_ict_officer_id: this.selectedOfficer.id,
              task_assigned_at: new Date().toISOString()
            }

            // Toast success
            this.showToast('Task assigned successfully', 'success')

            // Persist last known SMS status for Head of IT list to avoid Pending regression on refresh
            try {
              const cacheRaw = sessionStorage.getItem('headOfIt_smsStatusCache')
              const cache = cacheRaw ? JSON.parse(cacheRaw) : {}
              cache[this.requestId] = 'sent'
              sessionStorage.setItem('headOfIt_smsStatusCache', JSON.stringify(cache))
            } catch (e) {
              // Non-fatal: caching to sessionStorage failed; continue without blocking UX
              console.warn('HeadOfIT: failed to persist SMS status cache', e)
            }

            // Update officer status and SMS status locally
            const officerIndex = this.ictOfficers.findIndex((o) => o.id === this.selectedOfficer.id)
            if (officerIndex !== -1) {
              this.ictOfficers[officerIndex].status = 'Assigned'
              // Set SMS status to 'sent' (assuming successful notification)
              this.ictOfficers[officerIndex].sms_status = 'sent'
            }

            // Refresh officers list to get updated SMS status from backend
            setTimeout(() => {
              this.fetchIctOfficers()
            }, 2000)
          } else {
            alert('Failed to assign task: ' + result.message)
          }
        } catch (error) {
          alert('Error assigning task: ' + error.message)
        } finally {
          this.isAssigning = false
        }
      },

      viewProgress(officer) {
        // Navigate to progress view or show modal
        alert(`Viewing progress for ${officer.name} - Feature coming soon!`)
      },

      showToast(message, type = 'success') {
        const container = document.createElement('div')
        container.className = 'fixed top-4 right-4 toast-notification'
        container.style.zIndex = 9999
        const color = type === 'error' ? 'bg-red-600' : 'bg-green-600'
        container.innerHTML = `
          <div class="${color} text-white px-4 py-3 rounded-xl shadow-xl font-semibold">
            ${message}
          </div>
        `
        document.body.appendChild(container)
        setTimeout(() => {
          container.remove()
        }, 2500)
      },

      openCancelModal(officer) {
        this.cancelTarget = officer || {
          id: this.requestInfo?.assigned_ict_officer_id,
          name: 'Current ICT Officer'
        }
        this.showCancelModal = true
      },

      async cancelTask(officer) {
        try {
          this.isCancelling = true
          const result = await headOfItService.cancelTaskAssignment(this.requestId)
          if (result.success) {
            // Update local state
            if (officer?.id) {
              const officerIndex = this.ictOfficers.findIndex((o) => o.id === officer.id)
              if (officerIndex !== -1) this.ictOfficers[officerIndex].status = 'Available'
            }
            this.requestInfo = {
              ...(this.requestInfo || {}),
              assigned_ict_officer_id: null,
              task_assigned_at: null
            }
            this.showCancelModal = false
            // Toast success
            this.showToast('Task assignment cancelled', 'success')
          } else {
            this.showToast('Failed to cancel task: ' + result.message, 'error')
          }
        } catch (error) {
          this.showToast('Error cancelling task: ' + (error.message || 'Network error'), 'error')
        } finally {
          this.isCancelling = false
        }
      },

      getInitials(name) {
        if (!name) return 'NA'
        return name
          .split(' ')
          .map((n) => n[0])
          .join('')
          .toUpperCase()
          .slice(0, 2)
      },

      getStatusBadgeClass(status) {
        const statusClasses = {
          Available: 'bg-green-500 text-green-900',
          Assigned: 'bg-blue-500 text-blue-900',
          Busy: 'bg-yellow-500 text-yellow-900',
          Completed: 'bg-gray-500 text-gray-900'
        }
        return statusClasses[status] || statusClasses['Available']
      },

      // SMS Status methods
      getSmsStatusText(smsStatus) {
        const statusMap = {
          sent: 'Delivered',
          pending: 'Pending',
          failed: 'Failed'
        }
        return statusMap[smsStatus] || 'has no task'
      },

      getSmsStatusColor(smsStatus) {
        const colorMap = {
          sent: 'bg-green-500',
          pending: 'bg-yellow-500',
          failed: 'bg-red-500'
        }
        return colorMap[smsStatus] || 'bg-gray-400'
      },

      getSmsStatusTextColor(smsStatus) {
        const textColorMap = {
          sent: 'text-green-400',
          pending: 'text-yellow-400',
          failed: 'text-red-400'
        }
        return textColorMap[smsStatus] || 'text-gray-400'
      },

      hasService(request, serviceType) {
        if (!request?.request_types) return false

        if (Array.isArray(request.request_types)) {
          return request.request_types.some((type) =>
            type.toLowerCase().includes(serviceType.toLowerCase())
          )
        }

        if (typeof request.request_types === 'string') {
          return request.request_types.toLowerCase().includes(serviceType.toLowerCase())
        }

        return false
      },

      goBack() {
        this.$router.push(`/head_of_it-dashboard/both-service-form/${this.requestId}`)
      },

      goBackToRequests() {
        this.$router.push('/head_of_it-dashboard/combined-requests')
      },

      clearSearch() {
        this.searchQuery = ''
      },

      clearAllFilters() {
        this.searchQuery = ''
        this.statusFilter = ''
      },

      // Dropdown management methods
      toggleDropdown(officerId, event) {
        event?.stopPropagation()
        const willOpen = this.openDropdownId !== officerId
        this.openDropdownId = willOpen ? officerId : null
        if (!willOpen) return

        try {
          const button = event?.target?.closest('.three-dot-button')
          if (!button) {
            this.dropdownPlacement = 'below'
            this.dropdownCoords = { top: 0, left: 0 }
            return
          }
          const rect = button.getBoundingClientRect()
          const viewportHeight = window.innerHeight
          const viewportWidth = window.innerWidth
          const spaceBelow = viewportHeight - rect.bottom
          const spaceAbove = rect.top
          const estimatedMenuHeight = 180
          const menuWidth = this.dropdownWidth

          // Decide placement
          this.dropdownPlacement =
            spaceBelow < estimatedMenuHeight && spaceAbove > spaceBelow ? 'above' : 'below'

          // Compute left so that menu right aligns with button right but stays within viewport
          let left = rect.right - menuWidth
          left = Math.max(8, Math.min(left, viewportWidth - menuWidth - 8))

          // Compute top based on placement
          let top =
            this.dropdownPlacement === 'below'
              ? rect.bottom + 8
              : rect.top - estimatedMenuHeight - 8
          top = Math.max(8, Math.min(top, viewportHeight - 8))

          this.dropdownCoords = { top, left }
        } catch (e) {
          this.dropdownPlacement = 'below'
          this.dropdownCoords = { top: 0, left: 0 }
        }
      },

      calculateDropdownPosition(event) {
        if (!event || !event.target) return

        const button = event.target.closest('.three-dot-button')
        if (!button) return

        const rect = button.getBoundingClientRect()
        const isMobile = window.innerWidth <= 768
        const dropdownWidth = isMobile ? 160 : 192 // Smaller on mobile
        const dropdownHeight = 120 // Approximate height for 3 items

        // Calculate initial position
        let top = rect.bottom + 8 // 8px margin below button
        let left = rect.right - dropdownWidth // Align right edge with button

        // Get viewport dimensions
        const viewportWidth = window.innerWidth
        const viewportHeight = window.innerHeight
        const scrollTop = window.pageYOffset || document.documentElement.scrollTop

        // Adjust for scroll position
        top += scrollTop

        // Adjust horizontal position if too far right or left
        if (left < 10) {
          left = 10
        } else if (left + dropdownWidth > viewportWidth - 10) {
          left = viewportWidth - dropdownWidth - 10
        }

        // For mobile, center the dropdown better
        if (isMobile) {
          left = Math.max(10, Math.min(left, viewportWidth - dropdownWidth - 10))
        }

        // Adjust vertical position if too far down
        const bottomSpace = viewportHeight - (rect.bottom - scrollTop)
        if (bottomSpace < dropdownHeight + 20) {
          // Show above button instead
          top = rect.top + scrollTop - dropdownHeight - 8
        }

        // Ensure dropdown doesn't go above viewport
        if (top < scrollTop + 10) {
          top = scrollTop + 10
        }

        console.log('Dropdown position calculated:', {
          top,
          left,
          viewportWidth,
          viewportHeight,
          isMobile
        })
        this.dropdownPosition = { top, left }
      },

      closeAllDropdowns(event) {
        // Only close if clicking inside menu or on button, otherwise close
        if (
          event &&
          (event.target.closest('.dropdown-menu') || event.target.closest('.three-dot-button'))
        ) {
          return
        }
        this.openDropdownId = null
      },

      // Execute action based on key
      executeAction(actionKey, officer) {
        console.log('SelectIctOfficer: Executing action:', actionKey, 'for officer:', officer.id)
        this.openDropdownId = null

        switch (actionKey) {
          case 'assign_task':
            this.confirmAssignTask(officer)
            break
          case 'view_progress':
            this.viewProgress(officer)
            break
          case 'cancel_task':
            this.cancelTask(officer)
            break
          default:
            console.warn('Unknown action:', actionKey)
        }
      }
    }
  }
</script>

<style scoped>
  .sidebar-narrow {
    flex-shrink: 0;
  }

  .medical-glass-card {
    background: rgba(59, 130, 246, 0.15);
    backdrop-filter: blur(25px);
    -webkit-backdrop-filter: blur(25px);
    border: 2px solid rgba(96, 165, 250, 0.3);
    box-shadow:
      0 8px 32px rgba(29, 78, 216, 0.4),
      inset 0 1px 0 rgba(255, 255, 255, 0.1);
  }

  /* Three-dot menu enhancements */
  .three-dot-menu {
    position: relative;
    /* no z-index by default to avoid overlaying pagination/footer */
  }

  /* Removed old in-cell dropdown styling since menu is now in portal layer */
  .dropdown-menu-portal button:hover {
    transform: translateX(2px);
  }

  /* Ensure the footer/pagination remains above table body (unchanged) */
  .medical-glass-card > .p-6 + div .bg-blue-800\/30 {
    position: relative;
    z-index: 3500;
  }

  /* Ensure table cells do not clip absolutely-positioned children */
  td,
  th {
    overflow: visible !important;
  }

  /* Animation for dropdown appearance */
  .dropdown-menu {
    animation: dropdownFadeIn 0.15s ease-out;
    transform-origin: top right;
  }

  @keyframes dropdownFadeIn {
    from {
      opacity: 0;
      transform: scale(0.95) translateY(-5px);
    }
    to {
      opacity: 1;
      transform: scale(1) translateY(0);
    }
  }

  /* Hover effects for better UX */
  .dropdown-menu button:hover {
    transform: translateX(2px);
  }

  /* High contrast focus states for accessibility */
  .three-dot-button:focus {
    outline: 2px solid #3b82f6 !important;
    outline-offset: 2px;
  }

  .dropdown-menu button:focus {
    outline: 2px solid #3b82f6;
    outline-offset: -2px;
    background-color: #f3f4f6;
  }

  /* Mobile Responsive */
  @media (max-width: 768px) {
    .overflow-x-auto {
      -webkit-overflow-scrolling: touch;
    }

    .min-w-full {
      min-width: 800px;
    }

    .px-6 {
      padding-left: 1rem;
      padding-right: 1rem;
    }

    /* Mobile dropdown adjustments */
    .dropdown-menu {
      min-width: 10rem !important;
      max-width: 14rem !important;
      /* Position will be handled by JavaScript for mobile */
    }

    /* Increase touch target size on mobile */
    .three-dot-button {
      padding: 12px !important;
      min-width: 44px;
      min-height: 44px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .dropdown-menu button {
      padding: 12px 16px !important;
      font-size: 16px !important;
    }
  }

  /* Tablet adjustments */
  @media (min-width: 769px) and (max-width: 1024px) {
    .dropdown-menu {
      min-width: 11rem;
      max-width: 15rem;
    }
  }
</style>
